import Vue from 'vue'
import Router from 'vue-router'
import ToDoList from '@/components/ToDoList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/myTasks'
    },
    {
      path: '/myTasks',
      name: 'ToDoList',
      component: ToDoList
    },
    {
      path: '/inProgress',
      name: 'ToDoList',
      component: ToDoList
    },
    {
      path: '/completed',
      name: 'ToDoList',
      component: ToDoList
    }
  ]
})



