<template>
  <div>
    <nav>
    <div class="navBlock">
      <router-link to="/myTasks">My Tasks</router-link>
      <router-link to="/inProgress">In Progress</router-link>
      <router-link to="/completed">Completed</router-link>
      <span class="bottomLine"></span>
    </div>
  </nav>
  <div class="content">
    <div class="toDoList">
      <div class="addToDos" @click="showAddBlock()" v-if="!addStatus">
        <i class="fas fa-plus"></i> Add Task
      </div>
      <div class="toDos" v-else>
        <div class="briefInformation isEditLine">
          <input type="text" class="toDosTitle" placeholder="Type Something Here…" v-model="addToDos.title" autofocus>
          <input type="checkbox" id="star" class="star" v-model="addToDos.important">
          <label for="star"></label>
          <i class="fas fa-edit edit isEditIcon"></i>
        </div>
        <div class="otherInformation isEdit">
          <div class="deadLine">
            <i class="far fa-calendar-alt"></i> Deadline
            <div class="operating">
              <input placeholder="yyyy/mm/dd" type="text" onfocus="(this.type='date')"  onblur="(this.type='text')" v-model="addToDos.day">
              <input placeholder="hh:mm" type="text" onfocus="(this.type='time')"  onblur="(this.type='text')" v-model="addToDos.time">
            </div>
          </div>
          <div class="file">
            <i class="far fa-file"></i> File
            <div class="operating">
              <input type="file" id="uploadFile" class="uploadFile" @change="uploadFile">
              <label for="uploadFile">
                <i class="fas fa-plus-square plus"></i>
              </label>
              {{addToDos.file}}
            </div>
          </div>
          <div class="comment">
            <i class="far fa-comment-dots"></i> Comment
            <div class="operating">
              <textarea name="" id="" cols="30" rows="10" placeholder="Type your memo here…" v-model="addToDos.comments"></textarea>
            </div>
          </div>
          <button class="cancel" @click="cancelAddBlock()">
            <i class="fas fa-times"></i> Cancel
          </button><!--
          --><button class="add" @click="addTask()">
            <i class="fas fa-plus"></i> Add Task
          </button>
        </div>
      </div>
      <div class="toDos" v-for="(toDos, index) in filterToDoList">
        <div class="briefInformation" :class="{isImportant: toDos.important, isEditLine: toDos.edit}">
          <input type="checkbox" :id="'checked' + index" class="checkbox" v-model="toDos.complete">
          <label :for="'checked' + index"></label><!--
          --><input type="text" class="toDosTitle" placeholder="Type Something Here…" v-if="toDos.edit" v-model="toDos.title"><!--
          --><p class="toDosTitle" :class="{iscomplete: toDos.complete}" v-else>{{toDos.title}}</p>
          <input type="checkbox" :id="'star' + index" class="star"  v-model="toDos.important">
          <label :for="'star' + index"></label>
          <i class="fas fa-edit edit" :class="{isEditIcon: toDos.edit}" @click="toggleEdit(toDos)"></i>
          <div class="otherInformationIcon" v-if="!toDos.complete && !toDos.edit">
            <i class="far fa-calendar-alt date" v-if="toDos.day != ''"> {{toDos.day.slice(5)}}</i>
            <i class="far fa-file" v-if="toDos.file != ''"></i>
            <i class="far fa-comment-dots" v-if="toDos.comments != ''"></i>
          </div>
        </div>
        <div class="otherInformation" :class="{isEdit: toDos.edit}">
          <div class="deadLine">
            <i class="far fa-calendar-alt"></i> Deadline
            <div class="operating">
              <input type="text" placeholder="yyyy/mm/dd" v-model="toDos.day">
              <input type="text" placeholder="hh:mm"  v-model="toDos.time">
            </div>
          </div>
          <div class="file">
            <i class="far fa-file"></i> File
            <div class="operating">
              <input type="file" :id="'uploadFile' + index" class="uploadFile">
              <label :for="'uploadFile' + index">
                <i class="fas fa-plus-square plus"></i>
              </label>
              {{toDos.file}}
            </div>
          </div>
          <div class="comment">
            <i class="far fa-comment-dots"></i> Comment
            <div class="operating">
              <textarea name="" id="" cols="30" rows="10" v-model="toDos.comments"></textarea>
            </div>
          </div>
          <button class="cancel" @click="cancelSaveBlock(toDos)">
            <i class="fas fa-times"></i> Cancel
          </button><!--
          --><button class="add" @click="saveEditData(toDos)">
            <i class="fas fa-plus"></i> Save
          </button>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  props: {},
  name: 'MyTasks',
  data () {
    return {
      toDoList: [],
      addStatus: false,
      addToDos: {
        complete: false,
        edit: false,
        status: false,
        important: false,
        title: '',
        day: '',
        time: '',
        file: '',
        comments: '',
      }
    }
  },
  computed: {
    sortToDoList() {
      return this.toDoList.sort((a, b) => {
        return b.important - a.important;
      })
    },
    filterToDoList() {
      var filterType = this.$route.path.slice(1)
      var filterToDoList = this.sortToDoList
      switch(filterType) {
        case "myTasks": 
            filterToDoList
            break;
        case "inProgress":
            filterToDoList = filterToDoList.filter((item) => {
              return item.complete === false
            })
            break;
        case "completed":
            filterToDoList = filterToDoList.filter((item) => {
              return item.complete === true
            })
            break;
      }
      return filterToDoList;
    }
  },
  components: {},
  watch: {
    $route (to, from){
        var path = this.$route.path
        var allNav = document.querySelectorAll("a")
        var nav = document.querySelector(`a[href="#${path}"]`)
        allNav.forEach((nav) => {
          nav.style.color = '#00408B'
          nav.style.borderBottom = "0"
        })
        nav.style.color = "white"
        nav.style.borderBottom = "5px solid #00408B"
    }
  },
  mixins: [],
  methods: {
    resetTheFormData() {
      this.addToDos = {
        complete: false,
        edit: false,
        status: false,
        important: false,
        title: '',
        day: '',
        time: '',
        file: '',
        comments: '',
      }
    },
    uploadFile(event) {
      this.addToDos.file = event.target.files[0].name
    },
    addTask() {
      if(this.addToDos.title === ''){
        alert('Please enter your ToDo title.')
        return
      }
      this.toDoList.push(this.addToDos)
      this.resetTheFormData()
      this.addStatus = false
    },
    showAddBlock() {
      this.addStatus = true
    },
    cancelAddBlock() {
      this.resetTheFormData()
      this.addStatus = false
    },
    cancelSaveBlock(toDos) {
      toDos.edit = false
    },
    saveEditData(toDos) {
      toDos.edit = false
    },
    toggleEdit(toDos) {
      if(toDos.complete) return
      toDos.edit = !toDos.edit
    }
  },
  // Life cycle hook
  beforeCreate () {},
  mounted () {}
}
</script>

<style lang="sass">
nav
  width: 100%
  background: #4A90E2
  .navBlock
    width: 60%
    height: 76px
    display: flex
    margin: 0 auto
    position: relative
    justify-content: space-around
    a
      display: inline-block
      text-align: center
      width: 30%
      font-size: 24px
      line-height: 76px
      color: #00408B
      text-decoration: none
      transition: all .5s linear
    a:first-child
      color: white
      border-bottom: 5px solid #00408B
    @media screen and (max-width: 768px)
      width: 100%
      a
        font-size: 16px
.content
  width: 100%
  min-height: calc(100vh - 76px)
  background: #E1E1E1
  padding: 24px 0
  .toDoList
    width: 60%
    margin: 0 auto 24px auto
    .addToDos
      width: 100%
      height: 65px
      background: #FFF
      border: 2px solid #C8C8C8
      border-radius: 5px
      font-size: 24px;
      color: #C8C8C8;
      padding: 0 33px
      margin-bottom: 24px
      line-height: 65px
      cursor: pointer
    .toDos
      width: 100%
      margin-bottom: 8px
      border-radius: 5px
      overflow: hidden
      .briefInformation
        min-height: 75px
        background: #F2F2F2
        padding:  24px 32px
        position: relative
        *
          vertical-align: top
        input[type="checkbox"]
          display: none
        .checkbox + label
          display: inline-block
          text-align: center
          width: 24px
          height: 24px
          background: #FFFFFF;
          border-radius: 2px;
          cursor: pointer
        .checkbox:checked + label
          background: #4A90E2
          color: white
        .checkbox:checked + label::before
          font-family: FontAwesome
          content: '\f00c'
        .toDosTitle
          display: inline-block
          width: 60%
          font-size: 24px
          line-height: 24px
          margin-left: 16px
          overflow: hidden
          white-space: nowrap
          text-overflow: ellipsis
          background: transparent
          border: 0
          height: 28px
        ::placeholder
          color: black
        .iscomplete
          text-decoration: line-through
          color: #9B9B9B
        .star + label
          width: 24px
          height: 24px
          font-size: 24px
          cursor: pointer
          color: white
          -webkit-text-stroke: 2px black
          position: absolute
          top: 24px
          right: 15%
        .star:checked + label
          -webkit-text-stroke: 0
          color: #F5A623
        .star + label::before
          vertical-align: top
          font-family: FontAwesome
          content: '\f005'
        .edit
          font-size: 24px
          position: absolute
          top: 24px
          right: 5%
          cursor: pointer
        .edit:hover, .isEditIcon
          color: #4A90E2
        .otherInformationIcon
          line-height: 16px
          margin: 16px 0 -7px 30px
          i
            margin-left: 10px
      .isEditLine
        border-bottom: 3px solid #C8C8C8
      .isImportant
        background: #FFF2DC
      .otherInformation
        display: none
        padding: 24px 0 0 0
        background: #F2F2F2
        .deadLine,.file,.comment
          width: 80%
          margin: 0 auto 24px auto
        .deadLine
          input
            width: 160px
            height: 35px
            border: 0
            border-radius: 2px
            font-size: 16px
            padding-left: 16px
            @media screen and (max-width: 768px)
              width: 100%
              margin-bottom: 5px
        .file
          .plus
            font-size: 32px
            color: #C8C8C8
            cursor: pointer
          .plus:hover
            color: #4A90E2
          .uploadFile
            display: none
        .comment
          textarea
            width: 100%
            border: 0
            border-radius: 2px
            padding: 8px 16px
        .operating
          margin: 8px 0 0 20px
        .cancel,.add
          width: 50%
          height: 60px
          border: 0
          font-size: 24px
          cursor: pointer
        .cancel
          background: white
          color: red
        .add
          color: white
          background: #4A90E2
      .isEdit
        display: block
    @media screen and (max-width: 768px)
      width: 95%  

</style>



